import { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faXmarkCircle,
	faBookOpenReader,
	faFilePen,
} from "@fortawesome/free-solid-svg-icons";
import styles from "./tab-controllers.module.scss";
import { useEffect } from "react";
function TabViewer({ tab_array, Viewer, onTabClose, Enable_fn }) {
	// can luu lai thong tin tab dang hoat dong , khi tai lai se render lai dung noi dung tab do chu ko phai tab cuoi cung trong mang tab_array
	const [currentTabActive, setCurrentTabActive] = useState(() => {
		let data = JSON.parse(
			localStorage.getItem("tab_")
		)?.tab_is_active_before_;
		if (data) Viewer(data);
		return data || undefined;
	});
	useEffect(() => {
		localStorage.setItem(
			"tab_",
			JSON.stringify({ tab_is_active_before_: currentTabActive })
		);
	}, [currentTabActive]);

	// meu mang chua tab thay doi (do them hoac xoa) thi cap nhat lai tab active la tab moi dc them vao

	// logic duoi chua dung lam!
	useEffect(() => {
		let lastID = tab_array[tab_array.length - 1]?._id;
		Viewer(lastID);
		setCurrentTabActive(lastID);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [tab_array.length]);
	console.log("render - tab c");
	const handleTabClose = (tab) => {
		onTabClose(tab, tab._id === currentTabActive);
	};

	// cân sửa lại code để hiển thị file đc thêm vào
	return (
		<div className={styles["wrapper"]}>
			<div className={styles["tabs"]}>
				{tab_array.map((tab) => {
					return (
						<div
							key={tab._id}
							className={`${styles["tab_btn_wraper"]} ${
								currentTabActive === tab._id
									? styles["tabActive"]
									: ""
							}`}
						>
							<button
								className={styles["tab_btn"]}
								onClick={() => {
									setCurrentTabActive(tab._id);
									Viewer(tab._id);
								}}
							>
								{tab.name}
							</button>

							<button
								className={styles["tab_btn_delete"]}
								onClick={() => {
									handleTabClose(tab);
								}}
							>
								<FontAwesomeIcon icon={faXmarkCircle} />
							</button>
						</div>
					);
				})}
			</div>
			<div className={styles["Enable_fn_btns"]}>
				<div className={styles["btn-wrapper"]}>
					<input
						id='Enable_readnable'
						type='checkbox'
						onChange={(e) => {
							Enable_fn.setReadnableContent(
								e.target.checked,
								currentTabActive
							);
						}}
					/>
					<label htmlFor='Enable_readnable' title='reader'>
						<FontAwesomeIcon icon={faBookOpenReader} />
					</label>
				</div>
				<div className={styles["btn-wrapper"]}>
					<input
						id='Enable_edit_mode'
						type='checkbox'
						onChange={(e) => {
							Enable_fn.editingContent(
								e.target.checked,
								currentTabActive
							);
						}}
					/>
					<label htmlFor='Enable_edit_mode' title='edit content'>
						<FontAwesomeIcon icon={faFilePen} />
					</label>
				</div>
			</div>
		</div>
	);
}

export default TabViewer;
