import { useState } from "react";
import styles from "./color.module.scss";
import colors from "./colors";
function SelectColorPanel({ handleChangeElementStyles }) {
	// luu thuoc tinh can set cho target element
	const Props = [
		{ label: "color", prop: "color" },
		{ label: "bg-color", prop: "backgroundColor" },
		{ label: "br-color", prop: "borderColor" },
	];
	const [currentProp, setCurrentProp] = useState(Props[0]);
	return (
		<div className={styles["SelectColorPanel"]}>
			<div className={styles["header"]}>
				{Props.map((prop) => {
					return (
						<div
							key={prop.label}
							className={styles["header-radio-wrapper"]}
						>
							<input
								type='radio'
								name='props-item'
								value={prop.prop}
								id={prop.label}
								onChange={(e) => {
									setCurrentProp(e.target.value);
								}}
							/>
							<label htmlFor={`${prop.label}`}>
								{prop.label}
							</label>
						</div>
					);
				})}
			</div>
			<div className={styles["main"]}>
				{colors.map((color) => {
					return (
						<div key={color} className={styles["item-wrapper"]}>
							<input
								type='radio'
								name='color-item'
								value={color}
								id={color}
								onChange={(e) => {
									// set mau
									handleChangeElementStyles(
										currentProp,
										e.target.value
									);
								}}
							/>
							<label
								htmlFor={`${color}`}
								style={{ backgroundColor: `${color}` }}
							></label>
						</div>
					);
				})}
			</div>
		</div>
	);
}

export default SelectColorPanel;
