import {
	useState,
	useEffect,
	useRef,
	useImperativeHandle,
	forwardRef,
} from "react";
import { toast } from "react-toastify";
import { Db } from "../../../database/Db_new";
import styles from "./../main.module.scss";
import TabViewer from "../../TabController";

import PanelForm from "../EditPanel/Panel_form";
import Panel from "../EditPanel/Panel";

function NoteScreen(props, ref) {
	const PanelRef = useRef();
	const Panel_formRef = useRef();
	const [ReadnableContent, setReadnableContent] = useState(false);
	const [EditContent, setEditContent] = useState(false);
	// dùng containerRef.current.innerHTMl để thêm nội dung vào view\
	const containerRef = useRef();
	// luu cac tab dc mo
	const [tab, setTab] = useState(() => {
		// get tab data da dc luu truoc do
		return JSON.parse(localStorage.getItem("tab_array")) || [];
	});
	// luu lai trang thai moi khi tab moi dc them hoac xoa
	useEffect(() => {
		if (tab) localStorage.setItem("tab_array", JSON.stringify(tab));
	}, [tab]);

	// loi save ca the dang select (trang thai edit chua thuc su mat di)
	const handleTabClose = (_tab, canCloseFile) => {
		// neu tab dang active bi dong , thi xoa noi dung dang hien thi
		let Index = tab.findIndex((tab) => tab._id === _tab._id);
		let copyTab = [...tab];
		copyTab.splice(Index, 1);
		if (canCloseFile && EditContent) {
			Db.transaction("rw", Db.files, async function () {
				await Db.files
					.where("_id")
					.equals(_tab._id)
					.modify((content) => {
						content.data = containerRef.current.innerHTML;
					})
					.then(() => {
						containerRef.current.innerHTML = null;
						setTab(copyTab);
					});
				toast.success(`Saved [ ${_tab.name} ] successfully!`, {
					position: toast.POSITION.BOTTOM_CENTER,
				});
			}).catch(function (e) {
				console.error(e.stack);
			});
		} else {
			containerRef.current.innerHTML = null;
			setTab(copyTab);
		}
	};
	const ViewerFn = async (id) => {
		// set mac dinh khong o che do chinh sua
		//	setEditContent(false);
		// truy vấn content theo id.=> rerender lại ui với data.
		if (!id) {
			containerRef.current.innerHTML = "page blank";
		} else {
			let data = await Db.files.where("_id").equals(id).first();
			// ok , it work well!
			containerRef.current.innerHTML = data?.data;
		}
		return;
	};
	// them chuc nang tao va chinh sua ghi chu o day
	useEffect(() => {
		let Ct = containerRef.current;
		function handleContentClick(e) {
			if (ReadnableContent && !EditContent) {
				if (e.target.dataset.readnable) {
					const utterThis = new SpeechSynthesisUtterance(
						e.target.innerText
					);
					utterThis.lang = "zh-CN";
					speechSynthesis.speak(utterThis);
					utterThis.onend = function () {
						console.log("finish!");
					};
				}
			} else {
				if (EditContent) {
					Ct.style.cursor = "pointer";
					PanelRef.current.setTargetElement(e.target);
					Panel_formRef.current.setTargetElement(e.target);
				}
			}
		}
		if (Ct) {
			Ct.addEventListener("click", handleContentClick);
		}
		return () => {
			console.log("clean up");
			Ct?.removeEventListener("click", handleContentClick);
		};
	}, [EditContent, ReadnableContent]);

	const editingContent = (value, tabCurrentActive) => {
		// neu dang trong che do chinh noi dung
		if (EditContent && tabCurrentActive) {
			Db.transaction("rw", Db.files, async function () {
				await Db.files
					.where("_id")
					.equals(tabCurrentActive)
					.modify((content) => {
						content.data = containerRef.current.innerHTML;
					})
					.then(() => {
						toast.success(
							`Saved [ ${tabCurrentActive} ] successfully!`,
							{
								position: toast.POSITION.BOTTOM_CENTER,
							}
						);
					});
			}).catch(function (e) {
				console.error(e.stack);
			});
		}
		// sau khi cap nhat lai file thi tat che do chinh sua noi dung
		setEditContent(value);
	};
	useImperativeHandle(ref, () => {
		return {
			addNewTab: function (tab) {
				// check tab da ton tai hay chua , neu chua thi them tab vao va re-render lai tab ui => re-render content
				setTab((prev) => {
					let index = prev.find((item) => item._id === tab._id);
					if (!index) {
						toast.success(`Open [ ${tab.name} ] successfully!`, {
							position: toast.POSITION.BOTTOM_CENTER,
						});
						return [...prev, tab];
					} else {
						toast.info(`[ ${tab.name} ] is opend.`, {
							position: toast.POSITION.BOTTOM_CENTER,
							toastId: "custom-id",
						});
						return prev;
					}
				});
			},
		};
	});
	useEffect(() => {
		PanelRef.current?.setFn(Panel_formRef.current?.setTargetElement);
	}, []);
	return (
		<div className={styles["wrapper"]}>
			<TabViewer
				Viewer={ViewerFn}
				onTabClose={handleTabClose}
				tab_array={tab}
				Enable_fn={{
					setReadnableContent,
					editingContent,
				}}
			/>
			<div className={styles["main"]}>
				<div
					className={styles["flex-row"]}
					style={{ height: `${EditContent ? 70 : 100}%` }}
				>
					<div
						ref={containerRef}
						data-is_main_container
						className={styles["main-content"]}
					></div>
					{EditContent && <Panel ref={PanelRef} />}
				</div>

				{EditContent && <PanelForm ref={Panel_formRef} />}
			</div>
		</div>
	);
}
export default forwardRef(NoteScreen);
