import { useState, useEffect, useImperativeHandle, forwardRef } from "react";
import { trigger } from "../../../customEvent";
import { nanoid } from "nanoid";
import SelectColorPanel from "../SeclectColorPanel";
import styles from "./EditPanel.module.scss";
import Tippy from "@tippyjs/react/headless";

import { styles_file } from "./File_styles";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faTrashCan,
	faBookOpenReader,
	faFilePen,
	faArrowLeftRotate,
	faCubes,
	faPalette,
	faMagicWandSparkles,
} from "@fortawesome/free-solid-svg-icons";

function Panel(props, ref) {
	const [TargetElement, setTargetElement] = useState(null);
	useImperativeHandle(ref, () => {
		return {
			setTargetElement,
		};
	});
	//them node vao element selected
	const AddElement = (SelectedElement, component) => {
		let newElement;
		switch (component) {
			case "container":
				newElement = document.createElement("div");
				newElement.setAttribute("is_container", true);
				break;
			case "paragraph":
				newElement = document.createElement("p");
				newElement.setAttribute("contenteditable", false);
				break;
			case "text":
				newElement = document.createElement("span");
				newElement.style.display = "inline-block";
				newElement.setAttribute("contenteditable", false);
				break;
			default:
				break;
		}

		newElement.style.margin = "15px 0";
		newElement.style.minHeight = "15px";
		SelectedElement.appendChild(newElement);
		trigger("createElement", newElement);
		setTargetElement(newElement);
	};
	// xu ly xoa element
	const handleRemoveElement = (element) => {
		if (element.dataset.is_main_container) {
			console.log("không thể xóa!");
			return;
		}
		let parentNode = element.parentNode;
		element.remove();
		// foucus vao the cha cua node vua xoa
		setTargetElement(parentNode);
	};
	// ok
	const handleChangeElementStyles = (propName, val) => {
		if (TargetElement) TargetElement.style[propName] = val;
	};

	// khi element moi dc them vao thi phai thay doi phan tu da chon thanh phan tu moi duoc tao nay. (set lai TargetElement =>)
	// co the check loai element dc tao de them bot style prop cho phu hop.
	const Actions = [
		{
			id: nanoid(5),
			label: "components:",
			tags: ["container", "paragraph", "text"],
			fn: AddElement,
		},
		{
			id: nanoid(5),
			buttons: [
				{
					id: nanoid(5),
					icon: faBookOpenReader,
					title: "enable speaker",
					type: "checkbox",
					defaultChecked: TargetElement?.dataset.readnable || false,
					// hàm xử lý thêm xóa thuộc tính tùy chỉnh vào phần tử dựa vào giá trị của checkbox
					fn: function (v) {
						if (v) {
							TargetElement.setAttribute("data-readnable", true);
							return;
						}
						TargetElement.removeAttribute("data-readnable", true);
					},
				},
				{
					id: nanoid(5),
					icon: faFilePen,
					title: "editing element ",
					type: "checkbox",
					defaultChecked: TargetElement?.isContentEditable,
					fn: function (v) {
						if (v) {
							TargetElement.setAttribute("contenteditable", true);
							return;
						}
						TargetElement.removeAttribute("contenteditable");
					},
				},
				{
					id: nanoid(5),
					icon: faTrashCan,
					title: "remove element",
					type: "button",
					fn: handleRemoveElement,
				},
				{
					id: nanoid(5),
					icon: faArrowLeftRotate,
					title: "select parentNode",
					type: "button",
					fn: function (element) {
						setTargetElement(element.parentNode);
					},
				},
			],
		},
	];
	useEffect(() => {
		TargetElement?.classList.add(`${styles["selecedElement"]}`);
		return () => {
			TargetElement?.classList.remove(`${styles["selecedElement"]}`);
		};
	}, [TargetElement]);
	return (
		<div className={styles["wrapper"]}>
			{TargetElement && (
				<>
					{/* phần thêm nội dung */}
					<section className={styles["section_top"]}>
						{Actions.map((action) => {
							if (action.tags) {
								return (
									<Tippy
										key={action.id}
										placement='left-start'
										interactive
										hideOnClick={false}
										render={({ ...attrs }) => {
											return (
												<div
													className='tippy-wrapper'
													tabIndex='-1'
													{...attrs}
												>
													<div
														className={
															styles[
																"tippy-wrapper"
															]
														}
													>
														<span>
															{action.label}
														</span>
														<div
															className={
																styles[
																	"group-btns"
																]
															}
														>
															{action.tags.map(
																(tag) => {
																	return (
																		<button
																			className={
																				styles[
																					"tag-btn"
																				]
																			}
																			key={nanoid(
																				5
																			)}
																			onClick={() => {
																				action.fn(
																					TargetElement,
																					tag
																				);
																			}}
																		>
																			{
																				tag
																			}
																		</button>
																	);
																}
															)}
														</div>
													</div>
												</div>
											);
										}}
									>
										<div className={styles["div-center"]}>
											<FontAwesomeIcon icon={faCubes} />
										</div>
									</Tippy>
								);
							}
							if (action.buttons) {
								return (
									<div
										key={action.id}
										className={styles["buttons"]}
									>
										{action.buttons.map((button) => {
											if (button.type === "button") {
												return (
													<div key={button.id}>
														{!TargetElement.dataset
															.is_main_container && (
															<button
																title={
																	button.title
																}
																className={
																	styles[
																		"btn-remove-element"
																	]
																}
																onClick={() => {
																	button.fn(
																		TargetElement
																	);
																}}
															>
																<FontAwesomeIcon
																	icon={
																		button.icon
																	}
																/>
															</button>
														)}
													</div>
												);
											}
											return (
												<div key={button.id}>
													<div
														key={button.key}
														className={
															styles[
																"checkbox-wrapper"
															]
														}
													>
														<input
															id={`${button.id}`}
															type='checkbox'
															defaultChecked={
																button.defaultChecked
															}
															onChange={(e) => {
																return button.fn(
																	e.target
																		.checked
																);
															}}
														/>
														<label
															htmlFor={`${button.id}`}
															title={button.title}
														>
															<FontAwesomeIcon
																icon={
																	button.icon
																}
															/>
														</label>
													</div>
												</div>
											);
										})}
									</div>
								);
							}
						})}
						<Tippy
							placement='left-start'
							interactive
							hideOnClick={false}
							render={({ ...attrs }) => {
								return (
									<div
										className={styles["tippy-wrapper"]}
										tabIndex='-1'
										{...attrs}
									>
										<SelectColorPanel
											handleChangeElementStyles={
												handleChangeElementStyles
											}
										/>
									</div>
								);
							}}
						>
							<div className={styles["div-center"]}>
								<FontAwesomeIcon icon={faPalette} />
							</div>
						</Tippy>
					</section>
					{/* style cho thành phần */}
					<Tippy
						placement='left'
						interactive
						hideOnClick={false}
						render={({ ...attrs }) => {
							return (
								<div
									className='tippy-wrapper'
									tabIndex='-1'
									{...attrs}
								>
									<section
										className={styles["tippy-wrapper"]}
									>
										<span
											className={
												styles["section_heading"]
											}
										>
											Styles
										</span>
										<div className={styles["flex-col"]}>
											{styles_file.map((item) => {
												if (item.options) {
													return (
														<div
															key={item.id}
															className={
																styles["row"]
															}
														>
															<span>
																{item.label}
															</span>
															<select
																className={
																	styles[
																		"select-wrapper"
																	]
																}
																onChange={(e) =>
																	handleChangeElementStyles(
																		item.propName,
																		e.target
																			.value
																	)
																}
															>
																{item.options.map(
																	(
																		option
																	) => {
																		return (
																			<option
																				className={
																					styles[
																						"option"
																					]
																				}
																				key={nanoid(
																					5
																				)}
																				value={
																					option
																				}
																			>
																				{
																					option
																				}
																			</option>
																		);
																	}
																)}
															</select>
														</div>
													);
												}
												return (
													<div
														key={item.id}
														className={
															styles["row"]
														}
													>
														<span>
															{item.label}
														</span>
														<input
															className={
																styles[
																	"props-input"
																]
															}
															defaultValue={
																TargetElement
																	.style[
																	item
																		.propName
																] || ""
															}
															type={`${
																item.type ||
																"text"
															}`}
															onChange={(e) =>
																handleChangeElementStyles(
																	item.propName,
																	e.target
																		.value
																)
															}
														/>
													</div>
												);
											})}
										</div>
									</section>
								</div>
							);
						}}
					>
						<div className={styles["div-center"]}>
							<FontAwesomeIcon icon={faMagicWandSparkles} />
						</div>
					</Tippy>
				</>
			)}
		</div>
	);
}
export default forwardRef(Panel);
