import {
	useRef,
	useState,
	useEffect,
	useImperativeHandle,
	forwardRef,
} from "react";
import { on, off } from "../../../customEvent";

import styles from "./EditPanel.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashCan, faAdd } from "@fortawesome/free-solid-svg-icons";

///set default position base on selected element
function PanelForm(props, ref) {
	const [TargetElement, setTargetElement] = useState(null);

	useImperativeHandle(ref, () => {
		return {
			setTargetElement,
		};
	});

	useEffect(() => {
		on("createElement", ({ detail }) => {
			setTargetElement(detail);
		});
		TargetElement?.classList.add(`${styles["selecedElement"]}`);
		return () => {
			off("createElement", ({ detail }) => {
				setTargetElement(detail);
			});
			TargetElement?.classList.remove(`${styles["selecedElement"]}`);
		};
	}, [TargetElement]);

	const text_box = useRef();
	// ton tai nhieu bug
	const handleChangeElement = (text) => {
		// let isTextView = TargetElement?.dataset.text_view;

		let newText = text.replaceAll("{", '<span data-text_view="true" >');
		newText = newText.replaceAll("}", "</span>");
		TargetElement.insertAdjacentHTML("beforeend", newText);
	};

	const resetForm = (e) => {
		e.preventDefault();
		text_box.current.focus();
		text_box.current.value = "";
	};
	const submitForm = (e) => {
		e.preventDefault();
		text_box.current.focus();
		handleChangeElement(text_box.current.value);
	};
	return (
		<div className={styles["float-form-content"]}>
			<form onReset={resetForm} onSubmit={submitForm}>
				<textarea
					className={styles["textarea"]}
					placeholder='add content'
					ref={text_box}
				></textarea>
				<div className={styles["btn-group"]}>
					<button type='submit' className={styles["add-btn"]}>
						<FontAwesomeIcon icon={faAdd} />
					</button>
					<button type='reset' className={styles["reset-btn"]}>
						<FontAwesomeIcon icon={faTrashCan} />
					</button>
				</div>
			</form>
		</div>
	);
}

export default forwardRef(PanelForm);
